import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { Instance } from "../data";

@Component({
	selector: 'app-inst',
	templateUrl: './inst.component.html',
	styleUrls: ['./inst.component.css']
})

export class InstComponent implements OnInit
{
	instances: Instance[];

	constructor(private instService: ApiService) {}

	ngOnInit()
	{
		this.getInstances();
	}

	getInstances(): void
	{
		this.instService
			.getInstances()
			.subscribe(it => this.instances = it);
	}
}
