import { Injectable } from '@angular/core';
import { Instance, Stat } from "./data";
import { Observable, of } from 'rxjs';
import { MsgService } from "./msg.service";
import { HttpClient } from "@angular/common/http";

@Injectable({providedIn: 'root'})
export class ApiService
{
	private url = "http://8.12.16.153:8000";
	// private url = "http://127.0.0.1:8001";

	constructor(private http: HttpClient,
	            private msg: MsgService)
	{ }

	getInstances(): Observable<Instance[]>
	{
		return this.http.get<Instance[]>(this.url + "/api/instances");
	}

	getStat(id: string): Observable<Stat[]>
	{
		return this.http.get<Stat[]>(this.url + "/api/stat/" + id);
	}

	private log(message: string)
	{
		this.msg.add('[InstService] ' + message);
	}
}
